@import "../../base/variables/border";
@import "../../theme/mixin";
@import "../../animation/variable";
@import "../../theme/variable";
@import "keyframe";
@import "mixin";
@import "variable";

.loading {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(#000, .4);

  // å¨ç»åç¹
  &-dot {
    display: inline-block;
    width: $loading-dot-size;
    height: $loading-dot-size;
    border-radius: 50%;
    /* stylelint-disable no-unknown-animations */
    animation: loading-dot .8s 0s $animation-ease-in-out infinite;

    @include theme-prop(background-color, primary-on-light);
  }

  // ä¸¤ä¸ªåç¹ç¢°æåæ¢
  &-dot-switch {
    display: flex;

    &::before,
    &::after {
      content: "";
      display: inline-block;
      width: $loading-dot-size;
      height: $loading-dot-size;
      border-radius: 50%;
      opacity: .6;
      animation: loading-dot-switch 2s 0s $animation-ease-in-out infinite;

      @include theme-prop(background-color, primary-on-light);
    }

    &::before {
      position: absolute;
    }

    &::after {
      animation-delay: -1s;
    }
  }

  // ææ¡
  &-text {
    font-size: $loading-text-font-size;

    @include theme-prop(color, primary-on-light);
  }

  // çªå¸å¼å¨ç»
  &-text-curtain {
    &::before,
    &::after {
      content: attr(data-text);
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -($loading-text-font-size * $line-height-base) / 2;
      overflow: hidden;
      font-size: $loading-text-font-size;
      text-align: center;
      transform: translateX(-50%);
    }

    &::before {
      @include theme-prop(color, secondary-on-light);
    }

    &::after {
      height: 0;
      color: #fff;
      // animation-fill-mode
      // ï¼1ï¼noneï¼é»è®¤å¼ï¼åå°å¨ç»æ²¡å¼å§æ¶çç¶æ
      // ï¼2ï¼backwardsï¼è®©å¨ç»åå°ç¬¬ä¸å¸§çç¶æ
      // ï¼3ï¼both: æ ¹æ®animation-direction è½®æµåºç¨forwardsåbackwardsè§å
      // animation-direction: normal alternateãreverseãalternate-reverse
      // http://www.ruanyifeng.com/blog/2014/02/css_transition_and_animation.html
      // http://demones.github.io/web-learning/css3/animation/animation.html
      // https://www.cnblogs.com/aaronjs/p/4642015.html
      // https://designmodo.com/steps-css-animations/
      animation: loading-curtain 1s linear infinite alternate both;
    }
  }

  // å¾æ å¨ç»
  &-icon {
    display: block;
    width: $loading-icon-size;
    height: $loading-icon-size;
    background-repeat: no-repeat;
    background-size: 100%;
    animation: loading-icon 1s step-end infinite;

    @include loading-icon($grey-color-900);
  }

  // å¾æ å¨ç»ï¼é»è²èæ¯
  &-icon-reverse {
    display: flex;
    align-items: center;
    justify-content: center;
    width: $loading-icon-size * 2;
    height: $loading-icon-size * 2;
    border-radius: $border-radius;

    @include theme-prop(background-color, secondary-on-light);

    &::after {
      content: "";
      display: block;
      width: $loading-icon-size;
      height: $loading-icon-size;
      background-repeat: no-repeat;
      background-size: contain;
      animation: loading-icon 1s step-end infinite;

      @include loading-icon(#fff);
    }
  }

  // åºå loadingï¼éè¦è®¾ç½®ç¶åç´ æç¥ååç´ ä¸º .position-relative
  &-range {
    position: absolute;
  }

  // èæ¯ä¸ºéæç
  &-transparent {
    background-color: transparent;
  }
}
