@mixin make-row($gutter: $grid-gutter-width) {
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  margin-left: ($gutter / -2);
  margin-right: ($gutter / -2);

  // ææçåå« col çæ ·å¼è®¾ç½® border-box ä¸º border-box
  [class*="col"] {
    box-sizing: border-box;
  }
}

// è®¾ç½®åæ ·å¼
@mixin make-col($size, $columns: $grid-columns) {
  flex: 0 0 percentage($size / $columns);
  // è®¾ç½®æå¤§å®½åº¦å¯ä»¥ç¡®ä¿åå®¹ä¸ä¼æº¢åºï¼åªæ IE10+ å Firefox éè¦è¿æ ·è®¾ç½®
  // è Chrome å Safari ä¸éè¦è®¾ç½®
  max-width: percentage($size / $columns);
}

// å®ä¹ååç§» mixinï¼ç¨ margin-left è®¾ç½®å¶åç§»é
@mixin make-col-offset($size, $columns: $grid-columns) {
  $num: $size / $columns;

  margin-left: if($num == 0, 0, percentage($num));
}

// ååºå¼åéååºå¼å°è£æå¬å±çæ ·å¼
// ç¸ç­å®½åº¦å¯¹åºçæ ·å¼
/* stylelint-disable at-rule-no-unknown */
@mixin make-grid-columns-equal-width($columns, $infix: "") {
  // è®¾ç½®ææååºå¼çåæ ·å¼ï¼ä» 1 å° $columns
  @for $i from 1 through $columns {
    .col#{$infix}-#{$i} {
      @extend %grid-column;
    }
  }

  .col#{$infix},
  .col#{$infix}-auto {
    @extend %grid-column;
  }
}

// å¤ä¸ªæ ¼å­å¯¹åºçæ ·å¼
@mixin make-grid-columns-percentage($columns, $infix: "") {
  // åºäº Flexbox è®¾ç½®ç¸ç­å®½åº¦çåï¼ä¾å¦ .col-md ç­
  .col#{$infix} {
    flex-grow: 1;
    flex-basis: 0;
    max-width: 100%;
  }

  .col#{$infix}-auto {
    flex: 0 0 auto;
    width: auto;
    max-width: none;
  }

  @for $i from 1 through $columns {
    .col#{$infix}-#{$i} {
      @include make-col($i, $columns);
    }
  }

  // éæ°æå col ä½ç½®
  .order#{$infix}-first {
    order: -1;
  }

  @for $i from 1 through $columns {
    .order#{$infix}-#{$i} {
      order: $i;
    }
  }

  // è®¾ç½® offset ï¼ä» 0 å° $columns - 1
  @for $i from 0 through ($columns - 1) {
    @if not ($infix == "" and $i == 0) { // å¯¹äºæååºå¼åç¼ï¼å¹¶ä¸æ¯ç¬¬0ä¸ªï¼åæç¥ï¼ä¾å¦ .offset-xs-0
      .offset#{$infix}-#{$i} {
        @include make-col-offset($i, $columns);
      }
    }
  }
}

// å®ä¹çæ gird framework mixin
@mixin make-grid-columns($columns: $grid-columns, $gutter: $grid-gutter-width, $breakpoints: $responsive-breakpoints) {
  // å®ä¹åå¬å±æ ·å¼ï¼éç¨å ä½ç¬¦éæ©å¨
  %grid-column {
    position: relative;
    width: 100%;
    min-height: 1px; // é²æ­¢æ²¡åå®¹æ¶ï¼åå¡é·åå¹¶å°ä¸èµ·
    padding-left: ($gutter / 2);
    padding-right: ($gutter / 2);
  }

  @if $enable-responsive {
    @each $breakpoint in map-keys($breakpoints) {
      // è¿ååç¼ï¼æ¯å¦ï¼-md
      $infix: breakpoint-infix($breakpoint, $breakpoints);

      @include make-grid-columns-equal-width($columns, $infix);

      @include media-breakpoint-up($breakpoint, $breakpoints) {
        @include make-grid-columns-percentage($columns, $infix);
      }
    }
  } @else { // å¦æä¸æ¯ååºå¼çæåµ
    @include make-grid-columns-equal-width($columns);
    @include make-grid-columns-percentage($columns);
  }
}
