// é»è®¤å³å¯¹é½ï¼å¯ä»¥éè¿è®¾ç½® justify-content æ¥æ§å¶æ¾ç¤ºä½ç½®
.paging {
  display: flex;
  justify-content: flex-end;
  margin-top: $paging-margin-top;

  &-items {
    display: flex;
    align-items: center;
    // éç½® ul æ ·å¼
    margin: 0;
    list-style: none;
  }

  &-item {
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: $paging-item-width;
    height: $paging-item-height;
    padding: 0 $paging-item-padding-x;
    margin-left: $paging-item-margin-left;
    border: 1px solid $grey-300;
    border-radius: 2px;
    cursor: pointer;
    user-select: none;
    transition: border-color 200ms $animation-ease-in-out, color 200ms $animation-ease-in-out;

    &:first-child {
      margin-left: 0;
    }

    &:hover {
      @include theme-prop(color, primary);
      @include theme-prop(border-color, primary);
    }

    &.active {
      @include theme-prop(border-color, primary);
      @include theme(primary, false);
    }

    &.disabled {
      pointer-events: none;
      cursor: default;
      @include theme-prop(color, disabled-on-light);
    }
  }

  &-more {
    position: relative;
    border: 0;
    cursor: default;
    &::after {
      content: "";
      position: absolute;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      box-shadow: 6px 0 0 0, -6px 0 0 0;
      @include theme-prop(color, secondary-on-light);
      @include theme-prop(background-color, secondary-on-light);
    }
  }

  &-prev,
  &-next {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 2px;
      height: 2px;
      // sass-lint:disable indentation
      box-shadow: -4px -8px, -2px -8px, 0 -8px, 2px -8px, 4px -8px, 6px -8px, 8px -8px, // ä¸é¢ççº¿
      8px -6px, 8px -4px, 8px -2px, 8px 0, 8px 2px, 8px 4px, // å³ä¾§ççº¿
      -10px -2px, -8px -2px, -6px -2px, -4px -2px, -2px -2px, 0 -2px, 2px -2px, // ä¸é¢ççº¿
      2px 0, 2px 2px, 2px 4px, 2px 6px, 2px 8px, 2px 10px; // å³ä¾§ççº¿
    }
  }

  &-prev::before {
    transform: rotate(-135deg) scale(.5);
  }

  &-next::before {
    transform: rotate(45deg) scale(.5);
  }

  &-control {
    display: flex;
    align-items: center;
    // éç½® ul æ ·å¼
    margin: 0;
    list-style: none;
  }

  &-control-item {
    box-sizing: border-box;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: $paging-item-width;
    height: $paging-item-height;
    margin-left: $paging-item-margin-left;
    user-select: none;

    .select,
    .input {
      width: 45px;
      margin-left: .25rem;
      margin-right: .25rem;
    }
    .select-inner,
    .input-field {
      height: 2rem;
      padding: 0 .5rem 0 .25rem;

      &:hover,
      &:focus {
        @include theme-prop(color, primary);
        @include theme-prop(border-color, primary);
      }
    }
  }
}

// å¯¹äºåè¿ååéå é¡µ
.paging-prev.paging-more,
.paging-next.paging-more {
  border: 1px solid $grey-300;
  cursor: pointer;

  &::before {
    content: normal;
  }

  &:hover {
    @include theme-prop(border-color, primary);

    &::before {
      content: "";
    }
    &::after {
      content: normal;
    }
  }
}

// å¯¹äº a æ ç­¾
a.paging-item {
  @include theme-prop(color, primary-on-light);
  &:hover {
    text-decoration: none;
  }
}
