@import "./variable";
@import "./mixin";

// å³é­å¾æ 
.icon-pure-close {
  &::before {
    position: absolute;
    top: $icon-size / 2;
    left: 0;
    width: $icon-size;
    height: 1px;
    background-color: currentColor;
    transform: rotate(-45deg);
  }

  /* stylelint-disable at-rule-no-unknown */
  &::after {
    transform: rotate(45deg);

    @extend .icon-pure-close::before;
  }
}

