// Table style

@import "../../base/variables/spacing";
@import "./variable";
@import "../../theme/mixin";
@import "../../responsive/mixin";
@import "./mixin";

// Table éç½®æ ·å¼
table {
  border-collapse: collapse; // é²æ­¢åºç°åè¾¹æ¡
}

caption {
  @include theme-prop(color, primary-on-light);

  padding-top: $table-cell-padding;
  padding-bottom: $table-cell-padding;
  text-align: center;
}

th {
  text-align: inherit;
}

.table {
  width: 100%;
  max-width: 100%;
  margin-bottom: $spacer;
  background-color: $table-bg;

  th,
  td {
    padding: $table-cell-padding;
    border-bottom: $table-border-width solid $table-border-color;
    vertical-align: middle;
  }

  thead th {
    border-top: $table-border-width solid $table-border-color;
    border-bottom: ($table-border-width * 2) solid $table-border-color;
  }

  tbody + tbody {
    border-top: ($table-border-width * 2) solid $table-border-color;
  }

  // è®¾ç½®åµå¥ç table
  .table {
    background-color: $white;
  }
}

.table-sm {
  thead th,
  td {
    padding: $table-sm-cell-padding;
  }
}

// å ä¸ border
.table-bordered {
  border: $table-border-width solid $table-border-color;

  thead th,
  td {
    border: $table-border-width solid $table-border-color;
  }

  thead {
    th,
    td {
      border-bottom-width: (2 * $table-border-width);
    }
  }
}

// è¡äº¤æ¿æ¾ç¤ºå¯¹æ¯é¢è²
.table-striped {
  tbody tr:nth-of-type(odd) {
    background-color: $table-bg-striped;
  }
}

// hover æ ·å¼
.table-hover {
  tbody tr:hover {
    background-color: $table-hover-bg;
  }
}

// å®ä¹ä¸»é¢æ ·å¼
@if ($enable-custom-theme and $enable-custom-theme-for-table) {
  // ä¸»é¢è²
  @each $theme in ("primary", "secondary") {
    @include table-theme($theme);
  }

  // è¾å©é¢è²å¯¹åº
  //@each $theme in $theme-assist {
  //  @include table-theme($theme);
  //}
}

// ååºå¼ tables
// å¯¹äºå°å±å¹ç table éè¦æ·»å è¯¥æ ·å¼ï¼ä½¿æ¾ç¤ºæ´ç¾è§
.table-responsive {
  @include media-breakpoint-down(md) {
    display: block;
    width: 100%;
    overflow-x: auto;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    // é²æ­¢åºç°å border
    &.table-bordered {
      border: 0;
    }
  }
}
