// button style
@import "../../base/variables/form";
@import "../../animation/variable";
@import "../../animation/function";
@import "../../animation/mixin";
@import "../../theme/mixin";
@import "../../ripple/mixin";
@import "variable";
@import "mixin";

.btn {
  @include theme-prop(color, primary-on-light);
  // å¯¹äº ripple-impact ä½¿ç¨ ripple æææ ·å¼ï¼æä»¥éè¦æ .ripple-impact é¤æ
  // ripple-base ä¸­å·²ç»å äºè¿æ»¤ :not(.ripple-impact)
  @include ripple-base;

  position: relative;
  box-sizing: border-box;
  display: inline-block;
  min-width: $btn-min-width;
  height: $btn-height;
  padding: 0 $btn-padding-x;
  border: 0;
  border-radius: $input-border-radius;
  outline: 0;
  overflow: hidden;
  font-weight: $btn-font-weight;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  line-height: $btn-height;
  background: transparent;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  -webkit-appearance: none;

  &::-moz-focus-inner {
    padding: 0;
    border: 0;
  }

  &:not(.ripple-impact) {
    @include ripple-bg((pseudo: "::before", opacity: $btn-unfilled-ripple-opacity));
    @include ripple-bg((pseudo: "::after", opacity: $btn-unfilled-ripple-opacity));
  }

  // å¯¹äº a æ ç­¾çå¤ç
  &:hover {
    color: currentColor;
    text-decoration: none;
  }

  &:focus,
  &:active {
    outline: 0;
  }

  // disabled
  &:disabled,
  fieldset:disabled &,
  &.disabled {
    @include theme-prop(color, disabled-on-light);
    @include theme-prop(background-color, transparent);

    cursor: default;
    pointer-events: none;
  }
}

// æµ®éçï¼å¸èµ·åå»æé´å½±ææå¬ç¨æ ·å¼
.btn-raised,
.btn-unshadow {
  @include button-front($color: primary-on-dark);
  @include button-back($background-color: hint-on-light);

  // disabled
  @include button-disabled($btn-bg-disabled);
}

// æµ®éçï¼å¸èµ·æ ·å¼
.btn-raised {
  @include button-shadow;
  // disabled
  &:disabled,
  fieldset:disabled &,
  &.disabled {
    @include shadow(0);
  }
}

// outline æ ·å¼ï¼è®¾ç½®è¾¹æ¡
.btn-outline {
  border: 1px solid;
}

@if ($enable-custom-theme and $enable-custom-theme-for-btn) {
  // å®ä¹ä¸»é¢æ ·å¼
  @each $theme in ("primary", "secondary") {
    .btn-#{$theme} {
      @include btn-theme($theme);
    }
  }

  // è¾å©é¢è²
  @each $theme in $theme-assist {
    .btn-#{$theme} {
      @include btn-theme($theme);
    }
  }
}

// Sizing
.btn-lg {
  @include btn-size($btn-font-size-lg, $btn-height-lg);
}

.btn-sm {
  @include btn-size($btn-font-size-sm, $btn-height-sm);
}

// 100% å®½åº¦ï¼ å»æåè§
.btn-full {
  display: block;
  width: 100%;
  border-radius: 0;
}

// btn group
.btn-group {
  margin-left: -$btn-group-margin-x;
  margin-right: -$btn-group-margin-x;

  /* stylelint-disable no-descending-specificity */
  > .btn {
    margin-bottom: $btn-group-margin-x * 2;
    margin-left: $btn-group-margin-x;
    margin-right: $btn-group-margin-x;
  }
}
