// radio ç»ä»¶
@import "../../ripple/mixin";
@import "../../theme/mixin";
@import "function";
@import "mixin";

// åºæ¬æ ·å¼
.radio {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  border-radius: 50%;
  cursor: pointer;
  will-change: opacity, transform, border-color, background-color, color;

  // radio åå½¢å½¢ç¶
  &-background {
    position: absolute;
    box-sizing: border-box;
    display: inline-block;
    pointer-events: none;
    // å¨ç»
    transition: radio-leave(opacity), radio-leave(transform);
  }

  // å®ä¹ radio çå¤§å°å°è£æ mixinï¼æ¹ä¾¿å®å¶åå¤ç
  @include radio-size();

  &-circle-outer {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border: 2px solid;
    @include theme-prop(border-color, secondary-on-light);
    border-radius: 50%;
    // å¨ç»
    transition: radio-leave(border-color);
  }

  // å¾éåçæ ·å¼
  &-circle-inner {
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    transform: scale(0, 0);
    @include theme-prop(background-color, secondary-on-light);
    // å¨ç»
    transition: radio-leave(transform), radio-leave(background-color);
  }

  &-native-control {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
    cursor: inherit;
    opacity: 0;

    // è·å hover æç¦ç¹æ¶
    &:hover,
    &:focus {
      + .radio-background {
        .radio-circle-outer {
          @include theme-prop(border-color, primary-on-light);
        }
      }
    }
  }
}

// checked å disabled å¨ç»ææ
.radio-native-control:checked,
.radio-native-control:disabled {
  + .radio-background {
    transition: radio-enter(opacity), radio-enter(transform);

    .radio-circle-outer {
      transition: radio-enter(border-color);
    }

    .radio-circle-inner {
      transition: radio-enter(transform), radio-enter(background-color);
    }
  }
}

// éä¸­æ ·å¼
.radio-native-control:checked {
  + .radio-background {
    .radio-circle-outer {
      @include theme-prop(border-color, primary-on-light);
    }

    .radio-circle-inner {
      @include theme-prop(background-color, primary-on-light);
      transform: scale(.5);
    }
  }
}

// æ³¢çº¹æææ¶ ripple
.radio-ripple {
  .radio-background {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      @include theme-prop(background-color, primary-on-light);
      pointer-events: none;
      opacity: 0;
      transform: scale(0, 0);
      transition: radio-leave(opacity), radio-leave(transform);
    }
  }

  // è·åç¦ç¹æ¶
  .radio-native-control:focus {
    + .radio-background::before {
      opacity: .16;
      transform: scale(2, 2);
      transition: radio-enter(opacity), radio-enter(transform);
    }
  }
}

// disabled æ ·å¼
.radio-disabled {
  pointer-events: none;
  cursor: default;
}

.radio-native-control:disabled {
  + .radio-background {
    cursor: default;

    .radio-circle-outer {
      border-color: rgba(#000, .26);
    }

    .radio-circle-inner {
      background-color: rgba(#000, .26);
    }
  }
}

// å®ä¹ä¸»é¢æ ·å¼
@if ($enable-custom-theme and $enable-custom-theme-for-radio) {
  // ä¸»é¢è²
  @each $theme in ("primary", "secondary") {
    .radio-#{$theme} {
      @include radio-theme($theme);
    }
  }

  // è¾å©é¢è²å¯¹åº radio
  @each $theme in $theme-assist {
    .radio-#{$theme} {
      @include radio-theme($theme, $opacity: .64, $assist: true);
    }
  }
}

// Sizing
.radio-lg {
  @include radio-size($radio-outline-size: $radio-outline-size-lg, $radio-size: $radio-size-lg);
}

.radio-sm {
  @include radio-size($radio-outline-size: $radio-outline-size-sm, $radio-size: $radio-size-sm);
}
