@import "../../theme/mixin";

// ä¸»é¢åä¸ºèæ¯ãè¾¹æ¡ãå¤´é¨å hover ç­ï¼ä»¥åç»å
@mixin table-theme($theme) {
  .table-#{$theme} {
    &,
    > th,
    > td {
      @include theme-prop(background-color, $theme);
    }
  }

  //.table-hover {
  //  $hover-background: darken($background, 5%);
  //
  //  .table-#{$state} {
  //    @include hover {
  //      background-color: $hover-background;
  //
  //      > td,
  //      > th {
  //        background-color: $hover-background;
  //      }
  //    }
  //  }
  //}

  .table-striped-#{$theme} {
    tbody tr:nth-of-type(odd) {
      @include theme-prop(background-color, #{$theme}-lightest);
    }
  }

  .table-hover-#{$theme} {
    tbody tr:hover {
      @include theme-prop(background-color, #{$theme}-alpha);
    }
  }

  // å¤´é¨ä¸»é¢
  .table-thead-#{$theme} {
    thead tr {
      @include theme($theme, false);
    }
  }
}
