// input style
@import "../../base/variables/options";
@import "../../animation/variable";
@import "../../base/variables/form";
@import "../../base/mixins/box-shadow";
@import "../../ripple/mixin";
@import "../../theme/mixin";
@import "../../theme/variable";
@import "../../shadow/mixin";
@import "variable";
@import "function";
@import "mixin";

// å®ä¹å¨ Input åç´ å¤å±åè£¹ä¸
.input {
  position: relative;
  box-sizing: border-box;
  display: inline-block;

  // å¦æå å¥ label åè®¾ç½®é´è·
  label > .input-field,
  label + .input-field {
    margin-left: $input-margin-left;
  }
}

// å®ä¹å¨ Input åç´ ä¸
.input-field {
  box-sizing: border-box;
  width: 100%;
  height: $input-height;
  padding: $input-padding-y $input-padding-x;
  border: 1px solid $input-border-color;
  line-height: $input-line-height;
  background: none;
  background-clip: padding-box;
  will-change: opacity, transform, color;
  -webkit-appearance: none; // å»æ input é»è®¤çè¡ä¸ºåæ ·å¼è¡¨ç°
  -webkit-font-smoothing: antialiased; // æé¯é½¿ææ
  -moz-osx-font-smoothing: grayscale; // æé¯é½¿ææ

  @if $enable-rounded {
    border-radius: $input-border-radius;
  } @else {
    border-radius: 0;
  }

  // Placeholder
  &::placeholder {
    @include theme-prop(color, hint-on-light);
    opacity: 1;
    transition: input-transition(opacity);
  }

  // å®ä¹ `:focus` æ ·å¼
  &:focus {
    border-color: $input-focus-border-color;
    outline: none;
    transition: input-transition(opacity);

    // è·åç¦ç¹æ¶ï¼éè placeholder
    &::placeholder {
      opacity: 0;
    }
  }

  // Disabled
  &:disabled,
  &[readonly] {
    @include input-disabled;
  }

  // è®¾ç½® type="color" æ ·å¼
  &[type="color"] {
    padding: 0;
    border: 0;
  }

  // æ»å
  &[type="range"] {
    padding: 0;
    border: 0;
    -webkit-appearance: slider-horizontal;
  }
}

// æå¹³åè¾å¥æ¡ï¼å³åªæä¸æ¡åºè¾¹æ¡è¡¨å
.input-flat {
  .input-field {
    border-width: 0 0 1px;
    border-radius: 0;
  }
}

// full width input
.input-full {
  display: block;
  flex: 1;
  width: 100%;

  .input-field {
    display: block;
    width: 100%;
  }
}

// ç¹å»inputæ¶æç¤ºæå­ç¼©å°ä¸ç§»ï¼å¹¶æ¹åé¢è²ä¸ºé«äº®è²
.input-float {
  .input-field {
    height: auto;
    padding-top: $input-float-top;
    border-width: 0 0 1px;
    border-radius: 0;
    &::placeholder {
      opacity: 0;
    }
  }

  &-label {
    position: absolute;
    bottom: $input-padding-y;
    left: $input-padding-x;
    pointer-events: none;
    cursor: text;
    transform: translateY(-20%);
    transform-origin: left top;
    transition: input-transition(transform), input-transition(color), input-transition(font-size);
    @include theme-prop(color, hint-on-light);
  }

  // å½ input è¾å¥æ¡è·åç¦ç¹æ¶ï¼label åä¸ç§»å¨
  // .input-field:-webkit-autofill + .input-float-label
  .input-field:focus + .input-float-label,
  .input-field-focused + .input-float-label,
  .input-field:-webkit-autofill + .input-float-label {
    cursor: auto;
    transform: translateY(-90%) scale(.8, .8);
    @include theme-prop(color, secondary-on-light);
  }

  // Disabled
  .input-field:disabled,
  .input-field[readonly] {
    & + .input-float-label {
      @include theme-prop(color, disabled-on-light);
    }
  }
}

// textarea æ ·å¼
.input-textarea {
  .input-field {
    height: auto;
    resize: none;
  }
}

// å å¥ .input æ ·å¼åï¼å½è·åç¦ç¹æ¶ï¼placeholder åå®¹éè
// å¦ææ³æ¢å¤ input é»è®¤è¡ä¸ºæ¶ï¼å³å½è·åç¦ç¹æ¶ï¼placeholder åå®¹ä¸éèï¼åªæè¾å¥åå®¹åæéè
// è¿æ¶éè¦å ä¸æ ·å¼ .input-placeholder
.input-placeholder {
  .input-field {
    &::placeholder {
      transition: input-transition(color);
    }
    &:focus {
      &::placeholder {
        opacity: 1;
        @include theme-prop(color, secondary-on-light);
      }
    }
  }
}

// è¡¨åæ ¡éªæ ææ¶
.input-invalid {
  .input-field {
    @include theme-assist(border-color, error);
    @include theme-assist(color, error);
    // Placeholder
    &::placeholder {
      @include theme-assist(color, error);
    }

    &:disabled,
    &[readonly] {
      border-color: $grey-color-100;
      @include theme-prop(color, disabled-on-light);
      // Placeholder
      &::placeholder {
        @include theme-prop(color, disabled-on-light);
      }
    }
  }

  .input-float-label,
  .input-field:focus + .input-float-label,
  .input-field-focused + .input-float-label,
  .input-field:-webkit-autofill + .input-float-label {
    @include theme-assist(color, error);
  }
}

// è¾å¥æ¡å¸¦å¾æ 
.input-icon {
  position: absolute;
  top: 50%;
  cursor: pointer;
  transform: translateY(-50%);
}

.input-icon-start {
  .input-field {
    padding-left: $input-icon-field-padding;
  }

  .input-icon {
    left: $input-icon-pos-x;
    right: initial;
  }

  .input-float-label {
    left: $input-icon-field-padding;
  }
}

.input.input-icon-end {
  .input-field {
    padding-right: $input-icon-field-padding;
  }

  .input-icon {
    left: initial;
    right: $input-icon-pos-x;
  }

  .input-float-label {
    right: $input-icon-field-padding;
  }
}

// Sizing
.input-lg {
  @include input-size($input-font-size-lg, $input-height-lg, $input-padding-x-lg, $input-padding-y-lg);
}

.input-sm {
  @include input-size($input-font-size-sm, $input-height-sm, $input-padding-x-sm, $input-padding-y-sm);
}

// å®ä¹ä¸»é¢æ ·å¼
@if ($enable-custom-theme and $enable-custom-theme-for-input) {
  // ä¸»é¢è²
  @each $theme in ("primary", "secondary") {
    .input-#{$theme} {
      @include input-theme($theme);
    }
  }

  // è¾å©é¢è²å¯¹åº input
  @each $theme in $theme-assist {
    .input-#{$theme} {
      @include input-theme($theme);
    }
  }
}
