// tab style
@import "./variable";
@import "../../theme/variable";
@import "../../theme/mixin";
@import "./mixin";
@import "../../animation/function";

// tabs åºæ¬æ ·å¼ç± .tab-bar å .tab ç»æ
.tab-bar {
  position: relative;
  display: flex;
  flex-direction: row;

  &-left {
    justify-content: flex-start;
  }

  &-center {
    justify-content: center;
  }

  &-right {
    justify-content: flex-end;
  }

  &-between {
    justify-content: space-between;

    .tab {
      flex: 1;
    }
  }

  @include tab-size();
}

.tab {
  position: relative;
  display: inline-block;
  overflow: hidden;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;

  @include theme-prop(color, secondary-on-light);

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    transform: scale(0);
    transition: animation-leave-sharp(transform, $tab-transition-duration);
    will-change: transform;
    pointer-events: none;

    @include theme-prop(background-color, primary-on-light);
  }

  &.active {
    @include theme-prop(color, primary-on-light);

    // å½åä¸ºæ´»å¨èç¹æ¶ï¼æ¾ç¤ºåºé¨è¾¹æ¡
    &::after {
      transform: scale(1);
      transition: animation-enter(transform, $tab-transition-duration);
    }
  }

  &:hover {
    // a æ ç­¾
    text-decoration: none;

    @include theme-prop(color, primary-on-light);
  }

  &:focus {
    @include theme-prop(outline-color, secondary-on-light);
  }

  // disabled
  &.disabled {
    cursor: default;
    pointer-events: none;

    @include theme-prop(color, disabled-on-light);
  }
}

// Tab åºé¨çº¿æ¡å¹³æ»çç§»å¨
.tab-bar-smooth {
  .tab::after {
    content: normal;
  }

  &-line {
    position: absolute;
    bottom: 0;
    left: 0;
    height: 2px;
    transform-origin: left top;
    transition: animation-enter(transform, $tab-transition-duration);
    will-change: transform;
    visibility: hidden;

    @include theme-prop(background-color, primary-on-light);
  }
}

// Tab å¤§å°
.tab-bar-lg {
  @include tab-size($padding-x: $tab-padding-x-lg, $padding-y: $tab-padding-y-lg, $font-size: $tab-font-size-lg);
}

.tab-bar-sm {
  @include tab-size($padding-x: $tab-padding-x-sm, $padding-y: $tab-padding-y-sm, $font-size: $tab-font-size-sm);
}

// Tab ä¸»é¢
@if ($enable-custom-theme and $enable-custom-theme-for-tab) {
  // ä¸»é¢è²
  @each $theme in ("primary", "secondary") {
    .tab-bar-#{$theme} {
      @include tab-theme($theme);
    }
  }

  // è¾å©é¢è²å¯¹åº radio
  // Fixme æ²¡éä¸­é¢è²æç¹åæ·¡ï¼å¾åç»­å¤ç
  @each $theme in $theme-assist {
    .tab-bar-#{$theme} {
      @include tab-theme($theme, $assist: true);
    }
  }
}

