@import "../../responsive/variable";
@import "../../responsive/mixin";
@import "../grid/variable";

@mixin make-container($gutter: $grid-gutter-width) {
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
  padding-left: ($gutter / 2);
  padding-right: ($gutter / 2);
}

