/* stylelint-disable declaration-block-trailing-semicolon, block-closing-brace-newline-before */

@mixin hover {
  &:hover { @content }
}

// sass-lint:disable brace-style
@mixin hover-focus {
  &:focus,
  &:hover {
    @content
  }
}

