@import "../../base/variables/border";
@import "../../base/variables/font";
@import "../../base/variables/zindex";
@import "./variable";

@import "../../base/mixins/reset-text";
@import "../../base/mixins/border-radius";
@import "../../theme/mixin";

.tooltip {
  position: absolute;
  z-index: $zindex-tooltip;
  display: none;
  margin: $tooltip-margin;
  font-size: $font-size-sm;
  // æè¡å¤ç
  word-wrap: break-word;
  opacity: 0;

  // tooltip å¯å¨ä»»æåç´ ä¸­ä½¿ç¨ï¼è¿æ ·æå¯è½ä¼è¢«è¿äºç®æ åç´ ææ¬æ ·å¼æå½±å
  // è¿ééç½®ææ¬ç¸å³æ ·å¼
  @include reset-text();

  &.show {
    opacity: $tooltip-opacity;
  }

  .arrow {
    position: absolute;
    display: block;
    width: $tooltip-arrow-width;
    height: $tooltip-arrow-height;
  }

  &.tooltip-top {
    padding: $tooltip-arrow-width 0;

    .arrow {
      bottom: 0;
      left: 48%;
    }

    .arrow::before {
      content: "";
      margin-left: -($tooltip-arrow-width - 2);
      border-width: $tooltip-arrow-width $tooltip-arrow-width 0;

      @include theme-prop(border-top-color, primary-on-light);
    }
  }

  &.tooltip-right {
    padding: 0 $tooltip-arrow-width;

    .arrow {
      top: 40%;
      left: 0;
    }

    .arrow::before {
      content: "";
      margin-top: -($tooltip-arrow-width - 2);
      border-width: $tooltip-arrow-width $tooltip-arrow-width $tooltip-arrow-width 0;

      @include theme-prop(border-right-color, primary-on-light);
    }
  }

  &.tooltip-bottom {
    padding: $tooltip-arrow-width 0;

    .arrow {
      top: 0;
      left: 48%;
    }

    .arrow::before {
      content: "";
      margin-left: -($tooltip-arrow-width - 2);
      border-width: 0 $tooltip-arrow-width $tooltip-arrow-width;

      @include theme-prop(border-bottom-color, primary-on-light);
    }
  }

  &.tooltip-left {
    padding: 0 $tooltip-arrow-width;

    .arrow {
      top: 40%;
      right: 0;
    }

    .arrow::before {
      content: "";
      right: 0;
      margin-top: -($tooltip-arrow-width - 2);
      border-width: $tooltip-arrow-width 0 $tooltip-arrow-width $tooltip-arrow-width;

      @include theme-prop(border-left-color, primary-on-light);
    }
  }

  &.tooltip-auto {
    &[x-placement^="top"] {
      @extend .tooltip-top;
    }
    &[x-placement^="right"] {
      @extend .tooltip-right;
    }
    &[x-placement^="bottom"] {
      @extend .tooltip-bottom;
    }
    &[x-placement^="left"] {
      @extend .tooltip-left;
    }
  }

  .arrow::before {
    position: absolute;
    border-style: solid;
    border-color: transparent;
  }
}

// æ¾ç¤º tooltip åå®¹
.tooltip-inner {
  max-width: $tooltip-max-width;
  padding: $tooltip-padding-y $tooltip-padding-x;
  text-align: center;

  @include border-radius($border-radius);
  @include theme-prop(color, primary-on-dark);
  @include theme-prop(background-color, primary-on-light);
}
