// card-header card-body card-footer card-actions card-media

.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  border: 1px solid $card-border-color;
  border-radius: $card-border-radius;
  overflow: hidden;

  &-header,
  &-footer {
    padding: $card-spacing * .75 $card-spacing;
    background-color: $card-header-footer-bg;
  }

  &-header {
    border-bottom: 1px solid $card-border-color;
  }

  &-footer {
    border-top: 1px solid $card-border-color;
  }

  // åªä»ï¼æå¾çï¼è§é¢ç­
  // éºæ»¡æ´ä¸ªæé¨ååºå
  &-media-overlay {
    box-sizing: border-box;
    padding: $card-spacing;
  }

  &-media {
    width: 100%;
  }

  &-body {
    // https://developer.mozilla.org/zh-CN/docs/Web/CSS/flex
    // åç´ ä¼æ ¹æ®èªèº«çå®½åº¦ä¸é«åº¦æ¥ç¡®å®å°ºå¯¸ï¼ä¼èªè¡ä¼¸é¿ä»¥å¸æ¶flexå®¹å¨ä¸­é¢å¤çèªç±ç©ºé´ï¼
    // ä¹ä¼ç¼©ç­è³èªèº«æå°å°ºå¯¸ä»¥éåºå®¹å¨
    // è¿éä¸»è¦ä¸ºäºä½¿ footers è½å¨æåºé¨æ¾ç¤º
    flex: 1 1 auto;
    padding: $card-spacing;
  }

  &-title,
  &-subtitle {
    margin-bottom: $card-spacing * .75;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
  }

  &-title {
    font-size: $font-size-lg;
    font-weight: 500;
  }

  &-subtitle {
    @include theme-prop(color, secondary-on-light);

    margin-top: -$card-spacing / 2;
    font-size: $font-size-base;
  }

  &-text {
    margin: 0;
    font-size: $font-size-base;

    + .card-text {
      margin-top: $card-spacing / 2;
    }
  }

  &-actions {
    box-sizing: border-box;
    display: flex;
    padding: $card-spacing;

    .card-action {
      margin-right: $card-spacing;

      &:last-child {
        margin-right: 0;
      }
    }

    // åç´æ¾ç¤º
    &-vertical {
      flex-flow: column;
      align-items: flex-start;

      .card-action {
        margin: 0 0 $card-spacing;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  // å¤´å
  &-avatar-wrap {
    position: relative;
    padding: $card-spacing;

    .card-avatar {
      position: absolute;
      width: 2.5rem;
      height: 2.5rem;
      border-radius: 50%;
    }

    .card-title {
      margin-left: 3.5rem;
    }

    .card-subtitle {
      margin-left: 3.5rem;
    }
  }

  &-block {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;

    .card-media {
      width: auto;
      padding: $card-spacing;
    }
  }

  // card shadow
  &-shadow {
    @include shadow(2);

    border: 0;
  }

  // card tabs
  &-header-tabs {
    padding-top: 0;
    padding-bottom: 0;
  }
}

// å¯¹äºéå çåºåï¼éè¦è®¾ç½®padding-top: 0;
.card-avatar-wrap + .card-media,
.card-avatar-wrap + .card-body,
.card-avatar-wrap + .card-actions,
.card-body + .card-actions {
  padding-top: 0;
}

// å¡çç»
.card-group {
  display: flex;
  flex-direction: column;

  > .card {
    margin-bottom: $card-spacing / 2;
  }

  @include media-breakpoint-up(sm) {
    flex-flow: row wrap;

    > .card {
      // https://developer.mozilla.org/zh-CN/docs/Web/CSS/flex
      // Flexbugs #4: https://github.com/philipwalton/flexbugs#4-flex-shorthand-declarations-with-unitless-flex-basis-values-are-ignored
      // flex-basis å¿é¡»æå®åä½æ¥è§£å³è¯¥ bug
      flex: 1 0 0%;
      margin-bottom: 0;
      border-radius: 0;

      &:first-child {
        border-top-left-radius: $card-border-radius;
        border-bottom-left-radius: $card-border-radius;
      }

      &:last-child {
        border-top-right-radius: $card-border-radius;
        border-bottom-right-radius: $card-border-radius;
      }

      + .card {
        margin-left: 0;
        border-left: 0;
      }
    }
  }
}

// å¸¦é´è·çå¡çç»
.card-spacing {
  display: flex;
  flex-direction: column;

  .card {
    margin-bottom: $card-spacing;
  }

  @include media-breakpoint-up(sm) {
    flex-flow: row wrap;
    margin-left: -$card-spacing;
    margin-right: -$card-spacing;

    .card {
      display: flex;
      // Flexbugs #4: https://github.com/philipwalton/flexbugs#4-flex-shorthand-declarations-with-unitless-flex-basis-values-are-ignored
      // flex-basis å¿é¡»æå®åä½æ¥è§£å³è¯¥ bug
      flex: 1 0 0%;
      flex-direction: column;
      margin: 0 $card-spacing;
    }
  }
}

// å¡ç Columns å¸å±

.card-columns {
  .card {
    margin-bottom: $card-columns-margin;
  }

  @include media-breakpoint-up(sm) {
    column-count: $card-columns-count;
    column-gap: $card-columns-gap;

    .card {
      display: inline-flex; // é²æ­¢ä¸ä¸ªå¡çå¨è·¨åæ¾ç¤ºï¼è®¾ç½®ä¸º inline-block ä¹å¯ä»¥
      width: 100%; // å æ»¡æ´ä¸ªåºå
    }
  }
}
