.select {
  position: relative;
  box-sizing: border-box;
  display: inline-block;
  cursor: pointer;

  @include theme-prop(color, primary-on-light);

  // ç®­å¤´
  &::after {
    content: "";
    position: absolute;
    top: calc(50% - #{$arrow-size} / 2);
    right: $arrow-position-right;
    border: $arrow-size solid transparent;
    border-bottom-width: 0;
    pointer-events: none;

    @include theme-prop(border-top-color, secondary-on-light);
  }

  &:focus {
    outline: none;
  }

  // å¦æå å¥ label åè®¾ç½®é´è·
  label > .select-inner,
  label + .select-inner {
    margin-left: $input-margin-left;
  }
}

// å¯ä»¥ç¨å¨åçç select æ ç­¾ä¸
// ä¹å¯ç¨å¨æ¨¡æçæ ç­¾ä¸ï¼æ¯å¦ div
// è®¾ç½®çé«åº¦ï¼åè§ï¼è¾¹æ¡é¢è²è¦ä¸ input ä¿æä¸è´
.select-inner {
  position: relative;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: $input-height;
  padding: $input-padding-y $arrow-spacer $input-padding-y $input-padding-x;
  border: 1px solid $input-border-color;
  -webkit-font-smoothing: antialiased; // æé¯é½¿ææ
  -moz-osx-font-smoothing: grayscale; // æé¯é½¿ææ
  line-height: $input-line-height;
  background: none;
  transition: animation-leave(border-color, 150ms), animation-leave(background-color, 150ms);
  cursor: pointer;
  -webkit-appearance: none; // éç½® select æ ·å¼

  @if $enable-rounded {
    border-radius: $input-border-radius;
  } @else {
    border-radius: 0;
  }

  // å¯¹äº ie æµè§å¨è®¾ç½®è¯¥å±æ§ï¼éèæç®­å¤´æ¾ç¤º
  &::-ms-expand {
    display: none;
  }

  // ä¸º select è®¾ç½® focus æ ·å¼
  &:focus {
    border-color: $input-focus-border-color;
    outline: none;
  }

  // Disabled
  &:disabled {
    @include select-disabled;
  }
}

// æå¹³åè¾å¥æ¡ï¼å³åªæä¸æ¡åºè¾¹æ¡è¡¨å
.select-flat {
  .select-inner {
    border-width: 0 0 1px;
    border-radius: 0;
  }
}

// full width select
.select-full {
  display: block;
  flex: 1;
  width: 100%;

  .select-inner {
    width: 100%;
  }
}

// select å¤é
.select-multi {
  width: $select-multi-width;
  padding: 0;
  border: 1px solid;
  outline: none;
  -webkit-appearance: none;

  @include theme-prop(border-color, hint-on-light);

  &:disabled {
    .option-item {
      cursor: default;

      @include theme-prop(color, disabled-on-light);
    }
  }

  .option-group {
    margin-top: $select-option-group-spacing;
    padding-left: $select-option-group-spacing;
    font-weight: normal;

    @include theme-prop(color, hint-on-light);

    &:last-child {
      margin-bottom: $select-option-group-spacing;
    }

    .option-divider {
      margin-left: -$select-option-group-spacing;
    }

    .option-item {
      margin-left: -$select-option-group-spacing;
    }
  }

  .option-item {
    display: flex;
    align-items: center;
    padding: $select-option-item-padding-y $select-option-item-padding-x;
    cursor: pointer;

    @include theme-prop(color, primary-on-light);

    &:first-child {
      margin-top: $select-option-item-margin-top;
    }
    &:last-child {
      margin-bottom: $select-option-item-margin-bottom;
    }

    &:disabled {
      cursor: default;

      @include theme-prop(color, disabled-on-light);
    }
  }

  .option-divider {
    margin-bottom: $select-option-divider-margin-bottom;
    padding-top: $select-option-divider-padding-top;
    border-bottom: 1px solid $select-option-divider-border-color;
    font-size: 0;
  }
}

// ä»¥ä¸æ ·å¼ä¸ºæ¨¡æåçç select å®ç°

.select {
  &-menu.list-menu {
    top: 0;
    left: 0;
    z-index: 4;
    max-height: 100%;
    transform-origin: center center;
  }

  &-menu-fixed.list-menu {
    position: fixed;
  }

  // å·²ç»éæ©çææ¬å¼
  &-selected-text {
    overflow: hidden;
    white-space: nowrap;
    transition: animation-leave(opacity, 125ms), animation-leave(transform, 125ms);
  }

  &-scroll-lock {
    overflow: hidden;
  }
  // å¼¹åº select options
  &-open {
    .select-selected-text {
      opacity: 0;
      transform: translateY(8px);
      // sass-lint:disable indentation
      transition: animation-enter(opacity, 125ms, 125ms),
      animation-enter(transform, 125ms, 125ms);
    }
  }

  // disabled ç¶æ
  &-disabled {
    cursor: default;

    @include theme-prop(color, disabled-on-light);

    &::after {
      @include theme-prop(border-top-color, disabled-on-light);
    }

    select:disabled {
      cursor: default;
      pointer-events: none;
      user-select: none;
    }
  }
}

// å®ä¹ä¸»é¢æ ·å¼
@if ($enable-custom-theme and $enable-custom-theme-for-select) {
  // ä¸»é¢è²
  @each $style in ("primary", "secondary") {
    .select-#{$style} {
      .select-inner {
        @include theme-prop(border-color, #{$style}-lighter);

        &:focus {
          @include theme-prop(border-color, #{$style});
        }
      }
    }
  }

  // è¾å©é¢è²å¯¹åº input
  @each $theme in $theme-assist {
    $color: map-get($theme-assist-colors, $theme);

    .select-#{$theme} {
      .select-inner {
        @include theme-assist(border-color, #{$theme}-light);

        &:focus {
          @include theme-assist(border-color, #{$theme});
        }
      }
    }
  }
}
