@import "./variable";
@import "../../theme/mixin";
@import "../../theme/variable";
@import "../../animation/function";

// tab-bar å¤å± scroller
.tab-bar-scroller {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  width: 100%;
  overflow: hidden;
  background-color: inherit;

  // å¯æ»å¨çåºå
  &-frame {
    position: relative;
    display: flex;
    flex: 1;
    justify-content: flex-start;
    overflow: hidden;

    .tab-bar {
      transition: animation-enter(transform, $tab-transition-duration);
      will-change: transform;
    }

    // æ¸å fixme æ¸åææå¾æ¹è¿
    &-gradient {
      background-image: linear-gradient(to right, $grey-color-200 0%, transparent 15%, transparent 85%, $grey-color-200 100%);
    }
  }

  // å·¦å³æ»å¨æç¤ºå¨
  &-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    width: $tab-bar-scroller-indicator-width;
    cursor: pointer;
    visibility: hidden;

    @include theme-prop(color, secondary-on-light);

    &:hover {
      @include theme-prop(color, primary-on-light);
    }

    &-inner {
      color: inherit;
      text-decoration: inherit;
      cursor: inherit;

      &:focus {
        outline-color: inherit;
      }

      &:hover {
        color: inherit;
      }
    }

    // æ¾ç¤º
    .enabled {
      visibility: visible;
    }
  }
}
